.header{
    .header__wrapper{
        display: flex;
        justify-content: space-between;
        margin-top: 30px;
    }

    .header__nav{
        .nav__item{
            display: flex;
            justify-content: space-between;            
            padding-right: 30px ;

            .nav__items {
                padding-left: 30px;
                list-style-type: none;

                .nav__link{
                    text-decoration: none;
                    color: #000;
                }
                
            }
        }
    }
}
